import {Pipe, PipeTransform} from "@angular/core";

@Pipe({name: 'capital'})
export class CapitalizePipe implements PipeTransform {

    transform(value:any) {
        if (value) {
            return value.replace(/\b\w/g, symbol => symbol.toLocaleUpperCase())
        }
        return value;
    }

}


